import React, { Suspense, lazy } from 'react';
import { Routes, Route, Navigate, useLocation } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import styled from 'styled-components';
import LoadingSpinner from './components/LoadingSpinner';
import { SettingsProvider } from './context/SettingsContext';
import { AuthProvider } from './context/AuthContext';
import backgroundPattern from "./Images/bg_pattern.svg";
import { AnimatePresence } from 'framer-motion';
import ErrorBoundary from './components/ErrorBoundary';
import withTelegramAuth from './hoc/telegramAuth';

const Home = lazy(() => import('./pages/Home'));
const Leaderboard = lazy(() => import('./pages/Leaderboard'));
const CheckIn = lazy(() => import('./pages/CheckIn'));
const Invite = lazy(() => import('./pages/Invite'));
const Achievement = lazy(() => import('./pages/Achievement'));
const Quest = lazy(() => import('./pages/Quest'));
const Profile = lazy(() => import('./pages/Profile'));
const Settings = lazy(() => import('./pages/Settings'));

const AppWrapper = styled.div`
  min-height: 100vh;
  background-color: #000033;
  background-image: url(${backgroundPattern});
  background-size: cover;
  color: #ffffff;
`;

const ContentWrapper = styled.div`
  padding-bottom: 60px;
`;

const IS_DEVELOPMENT = process.env.NODE_ENV === 'development';

const wrapWithAuth = (Component) => IS_DEVELOPMENT ? Component : withTelegramAuth(Component);

const AuthenticatedCheckIn = wrapWithAuth(CheckIn);
const AuthenticatedHome = wrapWithAuth(Home);
const AuthenticatedLeaderboard = wrapWithAuth(Leaderboard);
const AuthenticatedInvite = wrapWithAuth(Invite);
const AuthenticatedAchievement = wrapWithAuth(Achievement);
const AuthenticatedQuest = wrapWithAuth(Quest);
const AuthenticatedProfile = wrapWithAuth(Profile);
const AuthenticatedSettings = wrapWithAuth(Settings);

function App() {
  const location = useLocation();

  return (
    <ErrorBoundary>
      <AuthProvider>
        <SettingsProvider>
          <AppWrapper>
            <ContentWrapper>
              <AnimatePresence mode="wait">
                <Suspense fallback={<LoadingSpinner />}>
                  <Routes location={location} key={location.pathname}>
                    <Route path="/" element={<AuthenticatedCheckIn />} />
                    <Route path="/home" element={<AuthenticatedHome />} />
                    <Route path="/leaderboard" element={<AuthenticatedLeaderboard />} />
                    <Route path="/invite" element={<AuthenticatedInvite />} />
                    <Route path="/achievement" element={<AuthenticatedAchievement />} />
                    <Route path="/quest" element={<AuthenticatedQuest />} />
                    <Route path="/profile" element={<AuthenticatedProfile />} />
                    <Route path="/settings" element={<AuthenticatedSettings />} />
                    <Route path="*" element={<Navigate to="/" replace />} />
                  </Routes>
                </Suspense>
              </AnimatePresence>
            </ContentWrapper>
            <ToastContainer position="bottom-right" />
          </AppWrapper>
        </SettingsProvider>
      </AuthProvider>
    </ErrorBoundary>
  );
}

export default App;