import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useAuth } from '../context/AuthContext';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL || 'http://localhost:3000';
const IS_DEVELOPMENT = process.env.NODE_ENV === 'development';

const MOCK_USER = {
  id: '12345',
  username: 'johndoe',
  first_name: 'John',
  photo_url: 'https://example.com/photo.jpg',
  xp: 1000,
  cpLevel: 5
};

const withTelegramAuth = (WrappedComponent) => {
  return (props) => {
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const [error, setError] = useState(null);
    const { setUser, setToken } = useAuth();

    useEffect(() => {
      const initTelegramAuth = async () => {
        let userData;

        if (window.Telegram && window.Telegram.WebApp) {
          const webApp = window.Telegram.WebApp;
          webApp.ready();

          const initData = webApp.initDataUnsafe;
          if (initData && initData.user) {
            userData = initData.user;
          } else {
            setError('Unable to get user data from Telegram WebApp');
          }
        } else if (IS_DEVELOPMENT) {
          console.warn('Telegram WebApp not detected. Using mock data for development.');
          userData = MOCK_USER;
        } else {
          setError('Telegram WebApp is not available');
        }

        if (userData) {
          try {
            let response;
            if (IS_DEVELOPMENT) {
              response = { data: { user: userData, token: 'mock_token' } };
            } else {
              response = await axios.post(`${API_BASE_URL}/auth/telegram`, userData);
            }

            setUser(response.data.user);
            setToken(response.data.token);
            setIsAuthenticated(true);
          } catch (error) {
            console.error('Authentication failed:', error);
            setError('Failed to authenticate. Please try again.');
          }
        }
      };

      initTelegramAuth();
    }, [setUser, setToken]);

    if (error) {
      return <div>Error: {error}</div>;
    }

    if (!isAuthenticated) {
      return <div>Authenticating...</div>;
    }

    return <WrappedComponent {...props} />;
  };
};

export default withTelegramAuth;